%h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

%titile-mobile-before-mini-logo {
  font-family: Roboto, Arial, sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
}

%mini-logo {
  height: 16px;
  width: 23px;

  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url(../images/logo-mini_miami.svg);
}
